.header-buttons {
  transition: all 1s;
}

.menu {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding-right: 1rem;
}

.menu span {
  transition: all 0.3s;
}

.menu li {
  width: 115px;
  height: 45px;
}

.menu a {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  transition: 0.4s;
  border-bottom: 1px solid transparent;
}

.menu a:hover {
  border-color: var(--primary-color);
}

a > .fa-solid {
  padding-right: 0.5em;
}

.social-contacts {
  display: flex;
  width: 90px;
  justify-content: space-around;
  margin: 0 auto;
}

.social-contacts li > a > i {
  font-size: 20px;
}

@media (width < 980px) {
  a > .fa-solid {
    font-size: 18px;
    padding-right: unset;
  }

  .menu li {
    padding-left: unset;
    width: 50px;
  }

  .menu span {
    visibility: collapse;
    opacity: 0;
    text-align: center;
  }

  .social-contacts li > a > i {
    font-size: 18px;
    padding: 0 1rem;
  }

  @media (width < 465px) {
    .header-buttons {
      transition: unset;
    }

    .footer-bar {
      border-top: 1px solid var(--primary-color);
      position: fixed;
      bottom: 0;
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      z-index: 1;
      background-color: rgba(0, 0, 0, 0.95);
      width: 100%;
      height: 63px;
      transition: all 2s;
    }

    .foot-links {
      display: flex;
      width: 100%;
      transition: ease-out 1s;
    }

    .foot-links li {
      list-style: none;
      padding: 0.6em 1rem;
      text-align: center;
      transition: ease 0.4s;
      width: 100%;
    }

    .foot-links > li > a > span {
      transition: ease 0.4s;
      font-size: 15px;
    }

    .foot-links li:hover {
      background-color: var(--primary-color);
      color: black;
    }

    .foot-links i {
      position: relative;
      font-size: 25px;
    }

    .social-contacts {
      width: 100px;
      justify-content: space-around;
      padding: unset;
      margin-right: unset;
      transition: ease 1s;
    }
    .social-contacts li > a > i {
      font-size: 25px;
      padding: unset;
    }
  }
}

@media (max-width: 370px) {
  .foot-links li {
    padding: 0.6em 0.5rem;
  }
}
