.testemonials {
  margin: 0 auto;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.testemonials-container {
  max-width: 1200px;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  overflow-y: auto;
}

.swiper {
  width: 320px;
  height: 310px;
}

.swiper-slide {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 18px;
  color: #fff;
}

.section__subtittle {
  font-family: "Titillium Web", sans-serif;
  font-size: 1.5rem;
  font-weight: 600;
  color: var(--primary-color);
  margin: 0 auto;
  width: 100%;
  display: block;
  text-align: center;
  padding-bottom: 2em;
}

.testemonial-card {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  width: 320px;
  height: 300px;
  word-break: keep-all;
  background-color: var(--background-layer);
  border-radius: 5px;
  box-shadow: 0px 0px 5px 2px var(--background-layer);
  -webkit-transition: all 0.5s ease-in;
  transition: all 0.5s ease-in;
  -moz-transition: all 0.5s ease-in;
  -o-transition: all 0.5s ease-in;
}

.testemonial-label {
  padding: 0.5rem;
  display: flex;
  box-shadow: 0px 0px 3px 2px var(--primary-color);
  background: rgb(37, 37, 37);
}

.testemonial-info {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
}

.ttt {
  padding: 0.5rem;
  text-align: justify;
  font-family: "Source Sans Pro", sans-serif;
}

.testemonial-profile-pic {
  border: 2px solid var(--primary-color);
  width: 80px;
  height: 80px;
  object-fit: cover;
  border-radius: 50%;
}

.mySwiper {
  width: 1200px;
}

.fixed-height {
  height: 500px;
}

.swiper-slide-testemonial {
  width: 450px;
}

@media (max-width: 495px) {
  .mySwiper {
    width: 375px;
    height: 320px;
    padding-bottom: 2%;
  }

  .card {
    width: 300px;
  }

  .testemonial-title {
    font-size: 3.2rem;
  }

  .swiper-slide-testemonial {
    width: 300px;
  }

  .fixed-height {
    height: unset;
  }
}

@media (max-width: 370px) {
  .mySwiper {
    width: 50vh;
  }
  .testemonial-title {
    font-size: 2.2rem;
  }

  .section__subtittle {
    font-weight: unset;
  }
}
